@keyframes opacity {
    from {opacity: 0;}
    to {opacity: 1;}
}

.modal-wrapper {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 1000;
    background: rgba(0,0,0,.2);
    animation: opacity 0.3s ease;

    .modal-bg {
        position: absolute;
        left: 50%;
        top: 50%;
        background: $color-white;

        &.static {
            width: 400px;
            padding: 30px 30px 20px;
            margin-left: -200px;
            margin-top: -128px;
            border-radius: 10px;
            text-align: center;

            span {
                display: block;
                font-size: 1.6rem;
                line-height: 1.5;
            }
        }
    
        .close-btn {
            position: absolute;
            right: 30px;
            top: 20px;
            width: 30px;    
            height: 30px;
            border:none;
            text-indent: -9999px;
            background: url(../../images/ico_close.svg) no-repeat;
            background-position: center;
        }
    
        .save-btn {
            width: 100%;
            height: 45px;
            margin-top: 30px;
            border: none;
            border-radius: 10px;
            background-color: $color-purple;
            color: $color-white;
            font-size: 1.5rem;
        }
    }
}